@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

:root {
	--Orange: hsl(26, 100%, 55%);
	--Pale-orange: hsl(25, 100%, 94%);

	--Very-vark-blue: hsl(220, 13%, 13%);
	--Dark-grayish-blue: hsl(219, 9%, 45%);
	--Grayish-blue: hsl(220, 14%, 75%);
	--Light-grayish-blue: hsl(223, 64%, 98%);
	--White: hsl(0, 0%, 100%);
	--Black: hsl(0, 0%, 0%);
}
.page {
    display: flex;
    justify-content: center;
    width: 100vw;
}


.page-container {
    display: flex;
    width: 80%;
    padding: 6rem 0;
}

.left-content, 
.right-content {
    flex: 50% 1;
    padding: 0 3rem;
}

.right-content {
    font-family: 'Kumbh Sans', sans-serif;
    margin: auto 0;
}

.right-content h5 {
    color: var(--Orange);
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 1.6rem;
    letter-spacing: .1rem;
}

.right-content h1 {
    color: var(--Very-vark-blue);
    font-size: 3rem;
    font-weight: 700;
    width: 90%;
    margin-bottom: 2.6rem;
}

.right-content p {
    color: var(--Dark-grayish-blue);
    font-size: 1.2rem;
    width: 90%;
    line-height: 1.6rem;
    margin-bottom: 2rem;
}

.right-content h2 {
    color: var(--Very-vark-blue);
    font-size: 2rem;
    font-weight: 700;
}

.right-content h3 {
    color: var(--Orange);
    font-size: 1rem;
    font-weight: 700;
    background-color: hsl(26, 100%, 55%, .2);
    padding: .5rem;
    border-radius: .25rem;
    margin-left: 1.2rem;
}

.discount {
    display: flex;
    margin-bottom: 1rem;
}

.right-content h4 {
    color: var(--Grayish-blue);
    font-size: 1rem;
    font-weight: 700;
    text-decoration: line-through;
}

.add-cart {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 2.5rem;
}

.amount {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: var(--Light-grayish-blue);
    width: 10rem;
    height: 3.4rem;
    padding: 1.2rem 1rem;
    border-radius: .4rem;
}

.number {
    font-weight: 700;
}

.icon-minus {
    height: 6px;
    width: 1rem;
    margin-top: 5px;
    cursor: pointer;
}

.icon-minus:hover {
    opacity: 75%;
}

.icon-plus {
    height: 1rem;
    width: 1rem;
    cursor: pointer;
}

.icon-plus:hover {
    opacity: 75%;
}

.add-btn {
    width: 15rem;
    height: 3.4rem;
    font-weight: 700;
    font-size: 1rem;
    color: var(--White);
    border: none;
    background-color: var(--Orange);
    border-radius: .5rem;
    padding-right: 1rem;
    cursor: pointer;
    transition: opacity .1s ease;
}

.add-btn:hover {
    opacity: .8;
    box-shadow: 0 10px 20px 0 hsla(26, 100%, 55%, .5);
}

.add-btn img {
    filter: brightness(1000%);
    position: relative;
    top: .2rem;
    margin-right: 1rem;
}

@media screen and (max-width: 1200px) {
    .page-container {
        width: 100%;
        padding: 0;
        height: 100%;
        flex-direction: column;
        grid-gap: 2rem;
        gap: 2rem;
    }

    .left-content {
        padding: 0;
    }

    .right-content {
        padding: 2rem;
    }

    .right-content h1 {
        font-size: 2.5rem;  
    }
 
    .price-product {
        display: flex;
        flex-direction: row;
    }

    .right-content h1 {
        margin-bottom: 2rem;
    }

    .right-content h4 {
        width: 100%;
        text-align: end;
    }

    .add-cart {
        flex-direction: column;
        margin-top: 1rem;
        margin-bottom: 2rem;
    }

    .amount {
        width: 100%;
    }

    .add-btn {
        width: 100%;
    }
}
.navbar {
    display: flex;
    position: -webkit-sticky;
    position: sticky;
    justify-content: center;
    width: 100vw;
}

.navbar-container {
    display: flex;
    align-items: center;
    width: 80%;
    padding-top: 2rem;
    border-bottom: 1px solid var(--Grayish-blue);
}

.mMenu {
    display: none;
    margin-right: 1rem;
}

.navbar-logo-container {
    flex: 15% 1;
    padding-bottom: 1.8rem;
}

.navbar-logo {
    font-family: 'Kumbh Sans', sans-serif;
    font-weight: 700;
    text-decoration: none;
    font-size: 2rem;
    color: var(--Black);
}

.nav-menu {
    display: flex;
    flex-direction: row;
    padding-left: 24px;
    flex: 65% 1;
    margin-top: .6rem;
}

.nav-item {
    margin-right: 4%;
}

.mNav-menu {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 60vw;
    background-color: var(--White);
    flex-direction: column;
    padding-top: 5rem;
}

.mNav-menu.active {
    display: flex;
}

.mNav-item {
    width: 100%;
    padding: 1rem 2rem;
}

.mNav-item:hover {
    background-color: rgba(0, 0, 0, .1);
}

.mNavbar-links {
    color: var(--Very-vark-blue);
    width: 100%;
    text-decoration: none;
    font-family: 'Kumbh Sans', sans-serif;
    font-weight: 700;
    font-size: 1.2rem;
}


.ddOverlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .7);
}

.ddOverlay.active {
    display: block;
}

.icon-menu {
    display: none;
    z-index: 1;
    margin-bottom: 1.3rem;
}


.nav-item::after {
    display: block;
    padding-top: 2rem;
    content: '';
    border-bottom: 4px solid hsl(26, 100%, 55%);  
    transform: scaleX(0);  
    transition: transform 250ms ease-in-out;
    cursor: pointer;
}

.nav-item:hover:after {
    transform: scaleX(1); 
}

.navbar-links {
    text-decoration: none;
    font-family: 'Kumbh Sans', sans-serif;
    color: var(--Dark-grayish-blue);
}

.navbar-end-container {
    display: flex;
    align-items: center;
    flex-direction: row;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: flex-end;
    flex: 10% 1;
    padding-bottom: 1rem;
}

.avatar img {
    width: 2.5rem;
}

.avatar {  
    width: 2.775rem;
    height: 2.775rem;
    border: 2px solid hsl(26, 100%, 55%, 0);   
    border-radius: 50%;
    transition: border .2s ease;
}

.avatar:hover {  
    border: 2px solid hsl(26, 100%, 55%, 100);  
}

.cart {
    display: flex;
    text-decoration: none;
}

.notification {
    visibility: hidden;
    font-family: 'Kumbh Sans', sans-serif;
    position: relative;
    font-size: .8rem;
    height: 1rem;
    right: 10px;
    top: -8px;
    color: var(--White);
    background-color: var(--Orange);
    padding: .1rem .6rem 0 .6rem;
    border-radius: .6rem
}

.notification.active {
    visibility: visible;
}

@media screen and (max-width: 1200px) {
    .navbar-container {
        width: 90%;
        padding-top: 1rem;
        border-bottom: 0;
    }

    .icon-menu {
        display: block;
    }

    .mMenu {
        display: block;
    }

    .nav-menu {
        display: none;
    }
}
.cart-dropdown {
    position: absolute;
    height: 16rem;
    width: 22rem;
    top: 5rem;
    right: 7.2%;
    background-color: var(--White);
    box-shadow: 0 10px 25px 0 rgba(0, 0, 0, 0.18);
    border-radius: .4rem;
    font-family: 'Kumbh Sans', sans-serif;
    transition: height .1s;
}

.header-cart {
    font-size: 1rem;
    font-weight: 700;
    width: 100%;
    padding: 1.5rem;
    border-bottom: 1px solid var(--Grayish-blue);
}

.empty {
    text-align: center;
    margin: 5rem 0;
    color: var(--Dark-grayish-blue);
    font-weight: 700;
}

.cart-item {
    padding: 1rem;
    display: flex;
}

.cart-item .cartProduct{
    width: 3rem;
    height: 3rem;
    border-radius: .2rem;
}

.cart-details {
    padding: .25rem 1rem;
}

.cart-details .title {
    color: var(--Dark-grayish-blue);
    margin-bottom: .5rem;
}

.cart-details .price {
    display: inline-block;
    color: var(--Dark-grayish-blue);
    margin-right: .5rem;
}

.cart-details .total {
    display: inline-block;
    font-weight: 700;
    color: var(--Very-vark-blue);
}

.deleteProduct {
    position: relative;
    width: 1rem;
    height: 1rem;
    right: 0;
    margin: auto 0 auto auto;
    cursor: pointer;
}

.checkout {
    display: none;
    color: var(--White);
    font-weight: 700;
    border: none;
    background-color: var(--Orange);
    text-align: center;
    width: 80%;
    padding: 1rem;
    margin: .5rem 10%;
    border-radius: .5rem;
    cursor: pointer;
}

.checkout.active {
    display: block;
}

@media screen and (max-width: 1200px) {
    .cart-dropdown {
        width: 96%;
        right: 2%;
    }

    .checkout {
        display: none;
        color: var(--White);
        font-weight: 700;
        border: none;
        background-color: var(--Orange);
        text-align: center;
        width: 96%;
        padding: 1rem;
        margin: .5rem 2%;
        border-radius: .5rem;
        cursor: pointer;
    }
}

.products-container {
    max-width: 512px;
}

.product-container {
    display: flex;
    flex-direction: column;
}

.product {
    display: none;
    width: 100%;
    border-radius: 1rem;
    cursor: pointer;
}

.product.show {
    display: flex;
}

.product.show:hover {
    opacity: .75;
}

.thumbnail-container {
    display: flex;
    margin-top: 2rem;
    justify-content: space-between;
    grid-gap: .5rem;
    gap: .5rem;
}

.thumbnail {
    max-width: 92px;
    max-height: 92px;
    border: 2px solid transparent;
    border-radius: 1rem;
    cursor: pointer;
    display: grid;
    place-items: center;
    background-color: transparent;
    overflow: hidden;
    padding: 0;
}

.thumbnail {
    width: 100%;
    height: 100%;
    transition: opacity .25s;
    background-color: rgb(255, 255, 255);
}

.thumbnail.active {
    border: 2px solid rgb(255, 125, 26);
}

.thumbnail:hover {
    opacity: 75%;
}

.mButton {
    display: none;
}

@media screen and (max-width: 1200px) {
    .products-container {
        max-width: 100vw;
    }

    .product {
        border-radius: 0;
    }

    .thumbnail-container {
        display: none;
    }

    .product.show:hover {
        opacity: 1;
    }

    .mButton {
        display: none;
        position: relative;
        height: 100%;
    }

    .mButton.active {
        display: flex;
    }

    .mPrevious-container {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 45% 0;
        left: 2rem;
        height: 54px;
        width: 54px;
        background-color: var(--Pale-orange);
        border-radius: 2rem;
        cursor: pointer;
    }

    .mNext-container {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 45% 0;
        right: 2rem;
        height: 54px;
        width: 54px;
        background-color: var(--Pale-orange);
        border-radius: 2rem;
        cursor: pointer;
    }
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 1000;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
}

.modal-product .M-product {
    display: none;
    width: 100%;
    border-radius: 1rem;
}

.modal-product .M-product.show {
    display: flex;
}

.modal-thumbnail {
    display: flex;
    width: 80%;
    margin: auto;
    margin-top: 2rem;
    justify-content: space-between;
    grid-gap: .5rem;
    gap: .5rem;
}

.modal-close {
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

.close {
    filter: brightness(1000%);
    width: 1.5rem;
    margin-bottom: 1rem;
    cursor: pointer;
}

.close:hover {
    filter: invert(50%) sepia(98%) saturate(559%) hue-rotate(342deg) brightness(97%) contrast(109%);
}

.modal-product {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 640px;
}

.modalProduct {
    display: none;
    width: 100%;
    border-radius: 1rem;
}

.modalProduct.show {
    display: flex;
}

.modal-thumbnail {
    display: flex;
    margin-top: 2rem;
    justify-content: space-between;
    grid-gap: .5rem;
    gap: .5rem;
}

.M-thumbnail {
    max-width: 92px;
    max-height: 92px;
    border: 2px solid transparent;
    border-radius: 1rem;
    cursor: pointer;
    display: grid;
    place-items: center;
    background-color: transparent;
    overflow: hidden;
    padding: 0;
}

.M-thumbnail.active {
    border: 2px solid rgb(255, 125, 26);
}

.M-thumbnail:hover {
    opacity: 75%;
}

.previous-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-top: 293px;
    margin-left: -27px;
    height: 54px;
    width: 54px;
    background-color: var(--Pale-orange);
    border-radius: 2rem;
    cursor: pointer;
}

.previous-icon {
    padding-right: 4px;
}

.previous-container:hover .previous-icon {
    filter: invert(50%) sepia(98%) saturate(559%) hue-rotate(342deg) brightness(97%) contrast(109%);
}

.next-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-top: 293px;
    margin-left: 613px;
    height: 54px;
    width: 54px;
    background-color: var(--Pale-orange);
    border-radius: 2rem;
    cursor: pointer;
}

.next-icon {
    padding-left: 3px;
}

.next-container:hover .next-icon {
    filter: invert(50%) sepia(98%) saturate(559%) hue-rotate(342deg) brightness(97%) contrast(109%);
}

@media screen and (max-width: 1200px) {
    .modal {
        display: none;
    }

    .overlay {
        display: none;
    }
}
